export default {
	data() {
		return {
			lists:[],
			scroll_view_height:0
		}
	},
	onReady() {
		let _this = this
		uni.getSystemInfo({
			success(res) {
				_this.phoneHeight = res.windowHeight
				console.log(res.windowHeight)
				// 计算组件的高度
				let view = uni.createSelectorQuery().select(".myfindview")
				view.boundingClientRect(data => {
					_this.scroll_view_height = _this.phoneHeight - data.top;
				}).exec()
			}
		});
		
		this.getList((data)=> {
			if(data)
			{
				this.lists = data;
			}
		});
	},
	methods: {
		gotoChongzhi(data){
			uni.navigateTo({
				url: '/pages/HuiYuanChongZhi/HuiYuanChongZhi?data='+JSON.stringify(data)
			});
		},
		resetPwd(data){
			uni.navigateTo({
				url: '/pages/huiYuanChongZhiMiMa/huiYuanChongZhiMiMa?data='+JSON.stringify(data)
			});
		},
		getList(callback)
		{
			this.bus.request({
				url: this.bus.url+'/agent/members',//?agentId=1
				method: 'GET',
				data: {
					agentId:this.bus.user.userId,
					// agentId:1,
					},
				success: res => {
					res = res.data;
					if(res.status=='1')
					{
						// uni.showToast({
						// 	title: '密码修改成功!',
						// 	duration: 2000
						// });
						callback(res.data)
					} else{
						uni.showToast({
							title: res.errMsg,
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					uni.showToast({
						title:'网络错误!',
						duration: 2000,
						icon:'none'
					});
					callback('')
				},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	},
	onLoad:function () {
		// uni.startPullDownRefresh();
	},
	onReachBottom:function()
	{//上拉加载更多
		// var current=this.lists.length;
		// var	size=10;
		// var this_ = this;
		// this.getList(current,size,function (data) {
		// 	for (var i = 0; i < data.length; i++) {
		// 		this_.lists.push(data[i]);
		// 	}
		// })
	},
	onPullDownRefresh:function () {
		//下拉刷新
		var current=1;
		var	size= 10;
		if(this.lists.length)
		{
			size=this.lists.length;
		}
		// var this_ = this;
		// this.getList(current,size,function (data) {
		// 	this_.lists=data;
		// })
		this.getList((data)=> {
			if(data)
			{
				this.lists = data;
			}
			uni.stopPullDownRefresh();
		});
		// setTimeout(function () {
		// 	uni.stopPullDownRefresh();
		// }, 1000);
	}
}
